body{
  font-family: 'Montserrat', sans-serif;
  margin: 0px 40px;
}

nav ul{
  display: flex;
  justify-content: flex-end;

}
nav ul li{
  list-style: none;
  margin: 0px 20px;

  
}
nav ul li a{
  text-decoration: none;
  color: black;
  font-weight: 500;
}
.active{
  color: rgb(148, 44, 64);
  font-weight: 650;
}
.profileImage{
  position: relative;
  top: 0;
  left: 45%;
  height: 150px;
  width: auto;
  border-radius: 50%;
  
}
/*styling blog component*/

.TodoForm{
  background-color: rgb(148, 44, 64);
  min-height: 500px;
  width: 600px;
  margin: auto;
}
#to-do-form input{
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  border-radius: 10px;
}
#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 10px;
  cursor: pointer;
  
}


/*styling contact*/
.contactForm{
  display: block;
  text-align: center;
  margin-bottom: 20px;
}
input[type=text] {
  width: 25%;
  margin-bottom: 10px;
  height: 2.25rem;
}
textarea{
  width: 25%;
  margin-bottom: 10px;
  height: 3.25rem;
}
input[type=submit] {
  background-color: rgb(148, 44, 64);
  color: white;
  border: 1px solid rgb(148, 44, 64);
  border-radius: 8px;
  padding: 8px 10px;
  width: 150px;
}
.contact h2{
  text-align: center;
}